import {Routes, Route, Navigate } from 'react-router-dom';
import './css/base.css';
import './css/app.css';
import Home from './pages/Home';
import UserDetails from './pages/UserDetails';


function App() {
  return (
    <>
			<main>
				<Routes>
					<Route exact path='/github-finder/' element={<Home />} />
					<Route exact path='/github-finder/users/:username' element={<UserDetails />} />
					<Route path='/github-finder/*' element={<Navigate to="/github-finder/" replace="true" />}/>
				</Routes>
			</main>
		</>
  );
}

export default App;